nav {
    position: sticky;
    top: 0;
    width: 100%;
    height: 50px;
    background: linear-gradient(45deg, rgb(156, 14, 156), midnightblue);
}

.liste {
    list-style-type: none;
    background: linear-gradient(45deg, rgb(156, 14, 156), midnightblue);
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 22px;
}

.items {
    margin: 0 15px;
    text-transform: uppercase;
    color: #f1f1f1;
    cursor: pointer;
    text-decoration: none;
}

.items:hover {
    transition: 0.4s ease-in-out;
    background-color: rgba(255, 255, 255, 0.2);
}

.active {
    color: rgba(255, 255, 255, 0.6);
}

.btn {
    display: none;
    position: absolute;
    top: 7px;
    right: 10px;
    padding: 5px;
    color: #000;
    font-size: 18px;
}

@media screen and (max-width: 500px) {
    .liste {
        flex-direction: column;
        height: auto;
    }

    .items:nth-child(1){
        margin-top: 50px;
    }

    .items {
        height: 70px;
        width: 100%;
        line-height: 70px;
        text-align: center;
        border-top: 1px solid rgba(255, 255, 255, 0.6);
    }

    .btn {
        display: block;
    }
}